.contenedor-juego {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.escenario {
  width: 100%;
  height: 100vh;
  display: block;
  background-color: #000;
}

.plataforma {
  fill: rgb(0, 120, 0);
  stroke-width: 0px;
  stroke: rgb(0, 120, 0);
}
.monta√±a {
  fill: rgb(0, 120, 0);
  stroke-width: 2px;
  stroke: rgb(0, 120, 0);
}

.tanque {
  fill: rgb(0, 120, 0);
}

.canon {
  stroke: #000;
  stroke-width: 5px;
}

.canon.activo {
  stroke: rgb(0, 120, 0);
  stroke-width: 3px;
}

.viento {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translate(-50%);
  color: #fff;
  font-size: 40px;
}

.direccion-viento {
  font-size: 24px;
  margin-left: 10px;
}

.botones {
  position: absolute;
  margin-top: 10px;
  top: 50px;
  width: 100%;
  background-color: #fff;
}

.bala {
  stroke: rgb(0, 120, 0);
  fill: rgb(0, 120, 0);
}

.impacto {
  stroke: rgb(0, 120, 0);
  fill: #000000;
}

.juego-nuevo {
  position: absolute;
  bottom: 10px;
  width: 20%;
  background-color: #fff;
}

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup-content {
  background-color: white;
  padding: 3rem;
  border-radius: 1rem;
  text-align: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.popup-content h2 {
  margin: 0 0 2rem 0;
}

.popup-content button {
  padding: 10px 20px;
  border: none;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
}

.popup-content button:hover {
  background-color: #0056b3;
}
